// ë‰´ìŠ¤ëª©ë¡ì„ ê°€ì ¸ì˜¤ëŠ” í•¨ìˆ˜ êµ¬í˜„
// ê¸€ ë‚´ìš©ì„ ë³´ë ¤ë©´ ê¸€ëª©ë¡ í™”ë©´ì—ì„œ ì œëª©ì„ í´ë¦­í–ˆì„ ë•Œ (aíƒœê·¸ê°€ ë°œë™ë˜ì—ˆì„ë•Œ)
// ìƒì„¸í˜ì´ì§€ë¡œ ì´ë™í•œë‹¤.
getNewsData = () => {
  const newsData = getData(TITLE_URL);
  const newsPage = [];
  newsPage.push('<ul>'); // ë¬¸ìì—´ <ul> ì‚½ì… - ì§ê´€ì 
  for (let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++) {
    newsPage.push(`
    <li>
      <a href="#/show/${newsData[i].id}">
        ${newsData[i].title}(â¤ï¸${newsData[i].comments_count})
      </a>
    </li>
  `);
  } //end of for
  newsPage.push('</ul>');
  newsPage.push(`
    <div>
      <a href="#/page/${
        store.currentPage > 1 ? store.currentPage - 1 : 1
      }"> ì´ì „ í˜ì´ì§€ </a>
      <a href="#/page/${
        store.currentPage < 3 ? store.currentPage + 1 : 3
      }"> ë‹¤ìŒ í˜ì´ì§€ </a>
    </div>
  `);
  container.innerHTML = newsPage.join('');
};

// ë‰´ìŠ¤ ìƒì„¸ë³´ê¸° í•¨ìˆ˜ êµ¬í˜„
newsDetail = () => {
  const id = location.hash.substring(7);
  console.log(id);
  const ncontent = getData(CONTENT_URL.replace('@id', id));
  container.innerHTML = `
    <h1>${ncontent.title}</h1>
    <div>
      <a href ="#/page/${store.currentPage}"> ëª©ë¡ìœ¼ë¡œ </a>
    </div>`;
};

// ë¼ìš°í„° í•¨ìˆ˜ êµ¬í˜„
router = () => {
  // ì‹¤ì œë¡œ ëª©ë¡ì„ ëˆ„ë¥´ë©´ #ì„ ê°–ëŠ”ë°
  // location.hashì— #ë§Œ ìˆìœ¼ë©´ ë¹ˆê°’ì„ ë°˜í™˜í•¨

  const routerPath = location.hash;
  if (routerPath === '') {
    //==ë‘ê°œë©´ ê°’ë§Œ ë¹„êµí•˜ëŠ”ë° ì„¸ê°œë©´ íƒ€ì…ê¹Œì§€ë„ ë¹„êµí•¨
    getNewsData();
  } else if (routerPath.indexOf('#/page/') >= 0) {
    store.currentPage = Number(routerPath.substring(7)); // ì „ì—­ë³€ìˆ˜ì˜ ê°’ì„ ë³€ê²½
    getNewsData(); // ê¸€ë‚´ìš©ë³´ê¸°
  } else {
    newsDetail();
  }
};

window.addEventListener('hashchange', router);
router();

